window.dom = {
    //ÁõÆÊ†áÊïàÊûú
    // ËæìÂÖ•create("<div><span>‰Ω†Â•Ω</span></div>")
    // Ëá™Âä®ÂàõÂª∫Â•ΩdivÂíåspan
    //ÂÆûÁé∞ÊÄùË∑ØÔºåÁõ¥Êé•ÊääÂ≠óÁ¨¶‰∏≤ÂÜôËøõInnerHTML
    // Áî®templateÊòØÂõ†‰∏∫Ëøô‰∏™Ê†áÁ≠æÈáåÂèØ‰ª•ÂÆπÁ∫≥ÊâÄÊúâÊ†áÁ≠æÔºå
    // divÊ†áÁ≠æÈáåÂ∞±‰∏çËÉΩÊîæ<tr></tr>Ê†áÁ≠æÔºåËÄåtemplateÂèØ‰ª•
    create(string) {
        const container = document.createElement("template")
        container.innerHTML = String.prototype.trim.call(string) // ÂéªÊéâÂ§ö‰ΩôÁ©∫Ê†º
        return container.content.firstChild
    },
    after(node, newNode) {
        // ÁõÆÊ†áÊòØÂú®NodeËäÇÁÇπÂêéÈù¢ÊèíÂÖ•node2ËäÇÁÇπ
        // ‰ΩÜÊòØDOMÂè™Êèê‰æõ‰∫ÜinsertBeforeÊé•Âè£
        // 1 -> 3
        // Âú®1ÂêéÈù¢ÊèíÂÖ•2, Á≠â‰ª∑‰∫éÂú®3ÁöÑÂâçÈù¢ÊèíÂÖ•2
        // ÊâÄ‰ª•Êàë‰ª¨ËΩ¨Êç¢‰∏∫Âú®nodeÁöÑ‰∏ã‰∏Ä‰∏™ËäÇÁÇπÁöÑÂâçÈù¢ÊèíÂÖ•node2
        node.parentNode.insertBefore(newNode, node.nextSibling)
    },
    before(node, newNode) {
        node.parentNode.insertBefore(newNode, node)
    },
    append(parent, node) {
        parent.appendChild(node)
    },
    wrap(node, newParent) {
        // ÊääNewparent ÊîæÂà∞nodeÂâçÈù¢
        // Êäänode appendÂà∞newparentÈáå
        // ÁõÆÊ†á: div1
        //        ‚Üì----> div2
        // ÂèòÊàê  div1
        //        ‚Üì----> div3
        //                ‚Üì----> div2
        node.before.call(node, node, newParent)
        newParent.append(node)
    },
    remove(node) {
        node.parentNode.removeChild(node)
        return node
    },
    // empty ÊääÊâÄÊúâÂ≠êËäÇÁÇπÂà†Êéâ
    // ÂùëÔºöchildNodes.lengthÊØèÊ¨°ÁöÑLength‰ºöÂèòÂåñ
    empty(node) {
        // const {childNodes} = node Á≠â‰ª∑‰∫éconst childNodes = node.childNodes
        const array = []
        let x = node.firstChild
        while (x) {
            array.push(dom.remove(node.firstChild))
            x = node.firstChild
        }
        return array
    },
    // Ê†πÊçÆÂèÇÊï∞ÁöÑ‰∏™Êï∞ÔºåÂÆûÁé∞‰∏çÂêåÁöÑÂáΩÊï∞ÔºåËøôÂè´ÂáΩÊï∞ÁöÑÈáçËΩΩ
    attr(node, name, value) {
        if (arguments.length === 3) {
            node.setAttribute(name, value)
        } else if (arguments.length === 2) {
            return node.getAttribute(name)
        }
    },
    text(node, string) {
        if (arguments.length === 2) {
            // ÈÄÇÈÖç‰∏çÂêåÊµèËßàÂô®
            if ('innerText' in node) { //ie
                node.innerText = string
            } else { // firefox / chrome
                node.textContent = string
            }
        } else if (arguments.length === 1) {
            if ('innerText' in node) { //ie
                return node.innerText
            } else { // firefox / chrome
                return node.textContent
            }
        }

    },
    html(node, string) {
        if (arguments.length === 2) {
            //‰øÆÊîπ
            node.innerHTML = string
        } else if (arguments.length === 1) {
            // Ëé∑ÂèñÂÜÖÂÆπ
            return node.innerHTML
        }
    },
    //ÊîπÊ†∑Âºè
    style(node, name, value) {
        if (arguments.length === 3) {
            // dom.style(div, 'color', 'red')
            node.style[name] = value
        } else if (arguments.length === 2) {
            if (typeof name === 'string') {
                //dom.style(test, 'border')
                // Ëé∑ÂèñÊüê‰∏™cssÂ±ûÊÄß
                return node.style[name]
            }

            if (name instanceof Object) {
                //dom.style(test, {border: '1px solid red', color: 'blue'})
                let object = name
                for (let key in object) {
                    // key : border / color
                    // node.style.border = ....
                    // node.style.color = ...
                    node.style[key] = object[key]
                }
            }
        }

    },
    class: {
        add(node, className) {
            node.classList.add(className)
        },
        remove(node, className) {
            node.classList.remove(className)
        },
        has(node, className) {
            return node.classList.contains(className)
        }
    },
    on(node, eventName, fn) {
        node.addEventListener(eventName, fn)
    },
    off(node, eventName, fn) {
        node.removeEventListener(eventName, fn)
    },
    // Ê†πÊçÆÈÄâÊã©Âô®Ëé∑ÂèñÂÖÉÁ¥†
    find(selector, scope) {
        let result = (scope || document).querySelectorAll(selector)
        return result.length === 1 ? result[0] : result
    },
    parent(node) {
        return node.parentNode
    },
    children(node) {
        return node.children
    },
    siblings(node) {
        // ÊàëÁà∂ÊØçÊâÄÊúâÂ≠©Â≠êÈáå‰∏çÊòØÊàëÁöÑÔºåÂ∞±ÊòØÊàëÁöÑÂÖÑÂºüÂßêÂ¶π
        return Array.from(node.parentNode.children).filter(n => n !== node)
    },
    next(node) {
        let x = node.nextSibling
        while (x && x.nodeType === 3) {
            // x ÊòØÊñáÊú¨
            x = x.nextSibling
        }
        return x
    },
    prev(node) {
        let x = node.previousSibling
        while (x && x.nodeType === 3) {
            // x ÊòØÊñáÊú¨
            x = x.previousSibling
        }
        return x

    },
    each(nodelist, fn) {
        for (let i = 0; i < nodelist.length; i++) {
            fn.call(null, nodelist[i])
        }
    },
    index(node) {
        let nodeList = dom.children(dom.parent(node))
        let i
        console.dir(nodeList)
        console.log('---node')
        console.log(node)
        for (i = 0; i < nodeList.length; i++) {
            console.log(nodeList[i])
            if (nodeList[i] === node) {
                return i
            }
        }
       return -1 //Ë°®Á§∫Ê≤°ÊâæÂà∞
    }


}

